}

.dropzone {
    max-width: 534px !important;
    overflow-x: auto;
    overflow-y: hidden;

    scrollbar-width: thin; /* Firefox */
    scrollbar-color: #888 #f1f1f1; /* Firefox */
}

/* (Chrome, Safari, etc.) */
.dropzone::-webkit-scrollbar {
    height: 6px;
}

.dropzone::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 3px;
}

.dropzone::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 3px;
}

.dropzone::-webkit-scrollbar-thumb:hover {
    background: #969696;
}

.dropzoneContainer input {
    display: none !important;
}

.dropzone .dz-message {
    color: var(--tw-primary);
}

#snippet-grid-dataGrid {
    overflow-x: auto !important;
}

.dz-preview {
    z-index: 0 !important;
    padding: .5rem;
    border-radius: 20px;
}

.form-control {
    display: flex;
    gap: .5rem;
    flex-direction: column;
}


.form-control input[type=checkbox] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: #fff;
    border: 1px solid #ccc;
    cursor: pointer;
    display: inline-block;
    position: relative;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    box-sizing: border-box;
    transition: all 0.2s ease-in-out;
    border-radius: .375rem;
}

.form-control .max-w-32 {
    max-width: unset !important;
}

.formLanguageSelect {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    line-height: 1;
    border-radius: .375rem;
    padding-block: .25rem;
    gap: .375rem;
    border: 1px solid transparent;
    font-weight: 500;
    font-size: .9rem;
    outline: none;
    color: var(--tw-primary);
    padding-inline: 1rem;
}

.formLanguageSelect option {
    color: #000000;
}

.formLanguageSelect:focus {
    box-shadow: 0 0 0 0.2rem rgba(var(--tw-primary-rgb, 0, 123, 255), 0.25);
}

.formLanguageSelect:disabled {
    opacity: 0.65;
    cursor: not-allowed;
}

.dropdown-content .menu-link {
    font-size: .85rem !important;
    padding-inline: .25rem !important;
    padding-block: .2rem !important;
}

