form.max-w-2xl{
    max-width: 100% !important;
}

.blockItem-value-edit{
    display: none;
}
.item.card.draggable:hover .blockItem-value-edit{
    display: block;
}
.item.card.draggable:hover .blockItem-value-show{
    display: none;
}

.page-bg {
    background-size: cover !important;
