.my-7 {
    margin-top: 1.75rem !important;
    margin-bottom: 1.75rem !important;
}

.px-7 {
    padding-left: 1.75rem !important;
    padding-right: 1.75rem !important;
}

.py-7 {
    padding-top: 1.75rem !important;
}

.pt-7 {
    padding-top: 1.75rem !important;
}

.pb-7 {
    padding-bottom: 1.75rem !important;
}


.modal.open {
    display: grid !important;
    align-items: center !important;
}

.modal-footer {
    padding-inline-start: 1.25rem;
    padding-inline-end: 1.25rem;
    padding-block-start: .625rem;
    padding-block-end: .625rem;
    outline: none;
}
.max-w-\\[1000px\\] {
    max-width: 1000px;
    width: 100%;
}

.flex.justify-end.mt-6 .btn.btn-primary {
    margin-bottom: 3rem;
}

.codex-editor {
    border: 1px solid !important;
    border-color: var(--tw-gray-400) !important;
    border-radius: .375rem !important;
    padding-inline-start: .75rem !important;
    padding-inline-end: .75rem !important;
    padding-block: .5rem;
}

.codex-editor__redactor {
    padding-bottom: 0px !important;
}

.ce-block__content,
.ce-toolbar__content {
    margin-inline: unset !important;
}


.accordion-content {
    /*max-height: 0;*/
    /*overflow: hidden;*/
    overflow: visible;
    opacity: 0;
    transition: max-height 0.4s ease, opacity 0.4s ease;
    visibility: hidden;
}

.accordion-content:not(.hidden) {
    opacity: 1;
    visibility: visible;
}

.accordion-toggle {
    transition: transform 0.3s ease;
    contain: layout style paint;
    cursor: pointer;
}

.accordion-active i {
    transform: rotate(180deg);
}

.accordion-toggle i {
    transition: transform 0.3s ease;
}

.tab-error-indicator {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.35rem;
    font-size: 0.7rem;
    width: 1rem;
    height: 1rem;
    border-radius: 999px;
    background: #dc3545;
    color: #fff;
    font-weight: bold;
}

/* DataGrid Responsive Styles */
.dataGrid .card-grid {
    width: 100% !important;
    max-width: 100% !important;
    overflow: hidden !important;
}

.dataGrid .card-body {
    padding: 0 !important;
    overflow: hidden !important;
}

.dataGrid .overflow-x-auto {
    overflow-x: auto !important;
    overflow-y: hidden !important;
    max-width: 100% !important;
    position: relative !important;
}

.dataGrid table {
    position: relative !important;
}

.dataGrid thead th.sticky {
    position: sticky !important;
    right: 0 !important;
    background-color: var(--tw-table-head-background-color);
    z-index: 15 !important;
}

.dataGrid tbody td.sticky {
    position: sticky !important;
    right: 0 !important;
    background-color: white !important;
    z-index: 15 !important;
    display: table-cell !important;
}

.dataGrid .sticky.has-shadow {
    box-shadow: -2px 0 4px rgba(0,0,0,0.1) !important;
}

/* Dark mode */
.dark .dataGrid thead th.sticky {
    background-color: var(--tw-gray-800) !important;
}

.dark .dataGrid tbody td.sticky {
    background-color: var(--tw-coal-600) !important;
}

.menu-item-hover-parent:hover {
    background-color: var(--tw-secondary-active) !important;
    border-radius: 0.5rem !important;
}
.dark .menu-item-hover-parent:hover {
    background-color: var(--tw-coal-300) !important;
}
.menu-item-hover-parent.active,
.menu-item-hover-parent.menu-item-active {
    background-color: var(--tw-secondary-active) !important;
    border-radius: 0.5rem !important;
}
.dark .menu-item-hover-parent.active,
.dark .menu-item-hover-parent.menu-item-active {
    background-color: var(--tw-coal-300) !important;
}
.menu-item-hover-parent:hover .menu-title {
    color: var(--tw-primary) !important;
    font-weight: 500 !important;
}
.menu-item-hover-parent:hover .menu-bullet::before {
    background-color: var(--tw-primary) !important;
}

tr:has(.menu-dropdown.show) {
  position: relative;
  z-index: 16;
}

.dataGrid table {
    position: relative !important;
    border-collapse: separate !important;
    border-spacing: 0 !important;
}

.dataGrid .sticky.has-shadow {
    box-shadow: -2px 0 4px rgba(0,0,0,0.1) !important;
}
